<template><section><h1>CircleGeometry、CircleOutlineGeometry</h1>
<ul>
<li><code v-pre>vc-geometry-circle</code> 组件用于加载圆形。</li>
<li><code v-pre>vc-geometry-outline-circle</code> 组件用于加载圆形轮廓。</li>
<li>需要作为 <code v-pre>vc-instance-geometry</code> 的子组件使用，可以挂载到 <code v-pre>vc-primitive</code> 或 <code v-pre>vc-primitive-ground</code>。</li>
</ul>
<h2>示例</h2>
<h3>加载圆形</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @LEFT_CLICK="LEFT_CLICK">
        <vc-primitive-ground :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes">
            <vc-geometry-circle ref="circle" :center="center" :radius="250000"></vc-geometry-circle>
          </vc-instance-geometry>
        </vc-primitive-ground>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-outline-circle ref="circleOutline" :center="centerOutline" :radius="250000"></vc-geometry-outline-circle>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span> <span class="hljs-attr">@LEFT_CLICK</span>=<span class="hljs-string">"LEFT_CLICK"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive-ground</span> <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-instance-geometry</span> <span class="hljs-attr">:geometry.sync</span>=<span class="hljs-string">"geometry"</span> <span class="hljs-attr">:attributes</span>=<span class="hljs-string">"attributes"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">vc-geometry-circle</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"circle"</span> <span class="hljs-attr">:center</span>=<span class="hljs-string">"center"</span> <span class="hljs-attr">:radius</span>=<span class="hljs-string">"250000"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-geometry-circle</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive-ground</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive</span> <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-instance-geometry</span> <span class="hljs-attr">:attributes</span>=<span class="hljs-string">"attributes"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">vc-geometry-outline-circle</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"circleOutline"</span> <span class="hljs-attr">:center</span>=<span class="hljs-string">"centerOutline"</span> <span class="hljs-attr">:radius</span>=<span class="hljs-string">"250000"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-geometry-outline-circle</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">vc-instance-geometry</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-primitive</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">appearance</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">attributes</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">geometry</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">center</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">102</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">38</span> },
        <span class="hljs-attr">centerOutline</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">110</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">38</span> }
      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([<span class="hljs-keyword">this</span>.$refs.circle.createPromise, <span class="hljs-keyword">this</span>.$refs.circleOutline.createPromise]).then(<span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
        <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'All geometries are loaded.'</span>)
