<h2>属性</h2>
<h3><code v-pre>vc-geometry-circle</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>center</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> 指定圆形中心点。 <strong>结构：{ lng: number, lat: number, height: number }</strong></td>
</tr>
<tr>
<td>radius</td>
<td>Number</td>
<td></td>
<td><code v-pre>required</code> 指定圆形半径。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定圆形参考椭球体。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定圆形离地表的高度（米）。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td><code v-pre>0.02</code></td>
<td><code v-pre>optional</code> 指定圆形圆弧每个点的角间距（弧度）。</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定要计算的顶点属性。</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定圆形拉伸的高度（米）。</td>
</tr>
<tr>
<td>stRotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定圆形纹理的旋转坐标（以弧度为单位）。 正旋转为逆时针方向。</td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre>vc-geometry-outline-circle</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>center</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> 指定圆形中心点。 <strong>结构：{ lng: number, lat: number, height: number }</strong></td>
</tr>
<tr>
<td>radius</td>
<td>Number</td>
<td></td>
<td><code v-pre>required</code> 指定圆形半径。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定圆形参考椭球体。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定圆形离地表的高度（米）。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td><code v-pre>0.02</code></td>
<td><code v-pre>optional</code> 指定圆形圆弧每个点的角间距（弧度）。</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定圆形拉伸的高度（米）。</td>
</tr>
<tr>
<td>numberOfVerticalLines</td>
<td>Number</td>
<td><code v-pre>16</code></td>
<td><code v-pre>optional</code> 指定圆形在拉伸时顶部与底部之间绘制的线数。</td>
</tr>
</tbody>
</table>
<hr>
<p>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CircleGeometry.html">CircleGeometry</a></strong>、<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CircleOutlineGeometry.html">CircleOutlineGeometry</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          attributes: null,
          geometry: null,
          center: { lng: 102, lat: 38 },
          centerOutline: { lng: 110, lat: 38 }
        }
      },
      mounted() {
        Promise.all([this.$refs.circle.createPromise, this.$refs.circleOutline.createPromise]).then((instances) => {
          console.log('All geometries are loaded.')
          const boundingSphereUnion = instances.reduce((prev, cur) => {
            const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            const boundingSphere = cur.vm.$parent.modelMatrix
              ? Cesium.BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
              : geometry.boundingSphere
            return prev === null ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
          }, null)
          instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        })
      },
      methods: {
        ready({ Cesium, viewer, cesiumObject }) {
          this.viewer = viewer
          const { ColorGeometryInstanceAttribute, PerInstanceColorAppearance } = Cesium
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random(), 0.5)
          }
          this.appearance = new PerInstanceColorAppearance({
            flat : true
          })
        },
        LEFT_CLICK(movement) {
          const feature = this.viewer.scene.pick(movement.position)
          console.log(feature)
        }
      }
    }
  </script>